import { Component } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Product, PRODUCT_DATA } from './models/product';
import { ReceiptItem } from './models/receipt-item';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'virtual-store';
  products: Observable<Product[]>;
  receipt: Observable<ReceiptItem[]>;

  hasItems = false;
  taxes = 0;
  total = 0;
  productColumns: string[] = ['name', 'price', 'exempt', 'imported'];
  receiptColumns: string[] = ['product', 'price', 'salestax', 'importduty', 'cost'];

  dataSource = PRODUCT_DATA;

  constructor() {

  }

  addItem() {
  }

  editItem() {
  }

  removeItem() {
  }

  testItems() {
  }

  clearItems() {
    this.receipt = null;
  }

  purchaseItems() {
    let prods = new Array<Product>();
    const items = new Array<ReceiptItem>();

    prods = PRODUCT_DATA;

    prods.forEach(p => {
      let i = new ReceiptItem();
      i.product = p;
      i.salestax = !p.exempt ? p.price * environment.sales_tax_rate : 0;
      i.importduty = p.imported ? p.price * environment.import_duty_rate : 0;
      i.cost = i.product.price + i.salestax + i.importduty;
      items.push(i);
    });

    this.receipt = of(items);
  }
}
